
import { useState, useEffect, useRef } from 'react';
import Pie from "./component/Pie";
import Bar from "./component/Bar";
import d from "./data.json";
import "./css/city_chart.css"

function App() {

  let city = [];
  let data = [];
  let table =[];
  let elLi = useRef([]);


  for(let key in d){
    if(key !== 'API' && key !== 'korea'){


    data.push({
      "countryNm":d[key].countryNm,
      "백신1차":d[key].vaccine_1['vaccine_1'],
      "백신2차":d[key].vaccine_2['vaccine_2'],
      "백신3차":d[key].vaccine_3['vaccine_3']
    });

    city.push({
        kor:d[key].countryNm,
        eng:key
    });

    table.push(
        {
            city:d[key].countryNm,
            v1:[
                d[key].vaccine_1['vaccine_1_old'],
                d[key].vaccine_1['vaccine_1_new'],
                d[key].vaccine_1['vaccine_1']
            ],
            v2:[
                d[key].vaccine_2['vaccine_2_old'],
                d[key].vaccine_2['vaccine_2_new'],
                d[key].vaccine_2['vaccine_2']
            ],
            v3:[
                d[key].vaccine_3['vaccine_3_old'],
                d[key].vaccine_3['vaccine_3_new'],
                d[key].vaccine_3['vaccine_3']
            ]
        }
    )
}
}
data.reverse();

//pie
let [cityNm,setCityNm] = useState('seoul');
let [countryNm,setCountryNm] = useState('서울');
let [p_data,setP_data] = useState();

useEffect(()=>{

    setP_data([
        {
          "id": "1차 접종자",
          "label": "백신 1차",
          "value": d[cityNm].vaccine_1['vaccine_1']
        },

        {
          "id": "2차 접종자", 
          "label": "백신 2차",
          "value": d[cityNm].vaccine_2['vaccine_2']
        },

        {
          "id": "3차 접종자",
          "label": "백신 3차",
          "value": d[cityNm].vaccine_3['vaccine_3']
        }

      ]);

},[cityNm]);

let [active,setActive] = useState(['active']);

function pieDataChange(e){

      //pie 데이터 바꾸기
      if(e.target.dataset.key) {


          setCityNm(e.target.dataset.key);
          setCountryNm(e.target.dataset.value);

          let n = e.target.dataset.num;
          let changeActive = [''];
          changeActive[n] = 'active';
          setActive( changeActive );

        }else{



        }
    }
    

  return (
      <>
        <div className ="pie">
            <div className="city_wrap">
                <div className="city_chart">
                    <h2>Corona-19-API-예방접종현황</h2>
                        <ul>
                            {city.map(function(v,k){
                                    return (
                                        <li 
                                         data-num={k}
                                         ref={elem => (elLi.current[k] = elem)}
                                         data-key={v.eng}
                                         data-value={v.kor}
                                         key={v.eng}
                                         className={active[k]}
                                         onClick={pieDataChange}>

                                            {v.kor}({v.eng})

                                        </li>
                                    )
                                })
                            }
                        </ul>
                </div>
            </div>

            <div className="chart_wrap">
                
                    <div className='graph'><Bar barData = {data}/></div>
                    <div className='donut'><Pie barData = {p_data} />
                    <div className='mini'>
                        {countryNm}
                    </div>
                    </div>
            </div>

        </div>
        <table>
            <thead>
                <tr>
                    <th colSpan={8}>"Corona-19-API-예방접종현황-v1</th>
                </tr>
                
                <tr>
                    <th rowSpan="2">시도명</th>
                    <th colSpan="2">1차</th>
                    <th colSpan="2">2차</th>
                    <th colSpan="2">3차</th>
                </tr>

                <tr>
                    <th>누적</th>
                    <th>신규</th>
                    <th>누적</th>
                    <th>신규</th>
                    <th>누적</th>
                    <th>신규</th>
                </tr>
            </thead>

            <tbody>
                {
                    table.map((v,k)=>(
                        <tr key={k}>
                            <td>{v.city}</td>
                            <td>{v.v1[0].toLocaleString('kr-KR')}명</td>
                            <td>{v.v1[1].toLocaleString('kr-KR')}명</td>
                            <td>{v.v2[0].toLocaleString('kr-KR')}명</td>
                            <td>{v.v2[1].toLocaleString('kr-KR')}명</td>
                            <td>{v.v3[0].toLocaleString('kr-KR')}명</td>
                            <td>{v.v3[1].toLocaleString('kr-KR')}명</td>
                        </tr>
                    ))

                }
            </tbody>
        </table>
      </>
  );


}


export default App;


